import mpopup from "../../../components/xuan-popup/xuan-popup.vue"
import { APPLYCASHOUT,MYCASH } from "../../../utils/api.js"
import WxValidate from '../../../utils/WxValidate.js';
export default {
	components:{mpopup},
	data() {
		return {
			mineWithDraw:"",
			myWalletData: '',
			cash:"",
			alipayAccount:"",
			idCard: '',
			realName: '',
			message:"",
			isDisable: false,
			isCheck:false
		}
	},
	onLoad() {
		uni.showToast({
			title:'加载中...',
			icon:'none',
			mask:true
		})
		this.mycash();
		this.initValidate();
	},
	methods: {
		// 初始化数据
		mycash(){
			const params = {}
			this.$request.url_request(MYCASH, params, "GET", res => {
				this.myWalletData = JSON.parse(res.data).data
				this.mineWithDraw = JSON.parse(res.data).data.balance
				uni.hideToast()
			}, err => {})
		},
		// 规则校验
		initValidate() {
			// 规则
			const rules = {
				alipayAccount: {
					required: true
				},
				cash: {
					required: true
					// checkcashTen: true
				},
				idCard: {
					required:true,
					idcard: true
				},
				realName: {
					required:true
				}
			}
			// 提示
			const messages = {
				alipayAccount: {
					required: "请输入提现账号！"
				},
				cash: {
					required: "请输入提现金额",
					checkcashTen: '提现金额不得小于10元'
				},
				idCard: {
					required: "请输入身份证号码",
					checkcashTen: '请输入身份证号码'
				},
				realName: {
					required:"请输入真实姓名"
				}
			}
			this.WxValidate = new WxValidate(rules, messages)
		},
		// 协议
		checkAgree(){
			this.isCheck = !this.isCheck
			this.$forceUpdate()
		},
		// 提现
		withdrawal(e){
			this.isDisable = true
			var _this = this
			setTimeout(function(){
				_this.isDisable = false
				_this.$forceUpdate()
			},1000)
			const params = e.detail.value
			if (!this.WxValidate.checkForm(params)) {
				const error = this.WxValidate.errorList[0]
				// 可以自定义提示
				uni.showToast({
					title: `${error.msg}`,
					icon: 'none'
				});
				return false
			} else {
				console.log(params)
				if(!this.isCheck){
					uni.showToast({
						title:'请阅读共享经济合作伙伴协议！',
						icon:'none'
					})
				} else{
				this.$request.url_request(APPLYCASHOUT,params,"GET",res=>{
					console.log(JSON.parse(res.data))
					var result = JSON.parse(res.data)
					if(result.code == 0){
						uni.showToast({
							title:"提现申请提交成功！",
							icon:"success",
							mask:true,
							duration:1200
						})
						setTimeout(() =>{
							uni.navigateBack({
							})
						},1200)
					} else if(result.code ==500){
						uni.showToast({
							title:result.msg,
							icon:"none",
							mask:true,
							duration:1200
						})
					}
				
				},err=>{})							
				}

				
			}	
			
		},
		cooperation(){
			uni.navigateTo({
				url:'../../../static/cooperationAgree/cooperationAgree'
			})
		},
		// totalCash全部
		totalCash(){
			this.cash = this.mineWithDraw
		},
		// 收起键盘
		pickupKeyboard(){
			uni.pageScrollTo({
				scrollTop:0,
				duration:0
			})
		},
		goback(){
			uni.navigateBack({
				delta:1
			})
		}
	}
}
